/*----------------------------------------------
Select 2
------------------------------------------------*/
.iq-custom-select .select2-container--bootstrap4 .select2-selection,
.select2-dropdown {
    background-color: var(--iq-bg1);
    border: none;
    border-radius: 0;
    color: #fff;
}

.select2-dropdown {
    top: 0;
    box-shadow: 0 12px 34px 0 rgba(19, 10, 10, 1);
}

.iq-custom-select select {
    box-shadow: none;
}

.select2-container--bootstrap4 .select2-dropdown .select2-results__option[aria-selected=true] {
    background-color: var(--iq-body-bg);
}

.select2-container--bootstrap4 .select2-results__option--highlighted,
.select2-container--bootstrap4 .select2-results__option--highlighted.select2-results__option[aria-selected=true] {
    background-color: var(--iq-primary);
    color: var(--iq-white);
}

.select2-container .select2-selection--single .select2-selection__rendered {
    line-height: calc(1.5em + 1.2rem);
    padding-left: 10px;
}

.select2-container--bootstrap4.select2-container--focus .select2-selection {
    box-shadow: none;
}

.select2-search--dropdown .select2-search__field {
    border-radius: 0;
    border: none;
    background: rgba(85, 85, 85, 0.65);
    color: var(--iq-white);
}

.select2-container--bootstrap4 .select2-selection--single .select2-selection__arrow b {
    border: none;
}

.select2-container--bootstrap4 .select2-selection--single .select2-selection__arrow b:before {
    content: '\ea41';
    position: absolute;
    font-family: "remixicon";
    top: 50%;
    transform: translateY(-50%);
    right: 1px;
    color: var(--iq-white);
    font-size: 16px;
    z-index: 99;
    transition: all 0.4s ease;
}

.select2-container--bootstrap4.select2-container--open .select2-selection__arrow b:before {
    transform: translateY(-50%) rotate(180deg);
}

.select2-container--bootstrap4 .select2-selection--single .select2-selection__arrow b {
    margin-top: 0;
}

.select2-container--bootstrap4 .select2-selection--single .select2-selection__arrow {
    width: auto;
}

.select2-container--bootstrap4 .select2-selection--single,
.select2-container--bootstrap4 .select2-selection--multiple {
    height: 45px !important;
}

.select2-container--bootstrap4 .select2-dropdown.select2-dropdown--above {
    border-radius: 0;
    border: none;
}

